/* Section 1 */
.mobile-home .section1 .content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.mobile-home .section1 .content .title{
    font-size: 22px;
    margin-bottom: 20px;
}
.mobile-home .section1 .content .desc{
    font-size: 14px;
    line-height: 24px;
}

/* Section 2 */
.mobile-home .section2 {
    background-color: #fff;
    color: #333;
    padding: 20px 20px 28px;
}
.mobile-home .section2 .content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.mobile-home .section2 .content .banner{
    width: 100%;
}

.mobile-home .section2 .paragraph {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 36px;
}

.mobile-home .section2 .content .title{
    font-size: 22px;
    margin-bottom: 16px;
}

.mobile-home .section2 .content .desc{
    font-size: 14px;
    line-height: 24px;
}

.mobile-home .section2 .content .button2{
    margin-top: 16px;
}

/* Section 3, Section 4 */
.mobile-home .section4 .content,
.mobile-home .section3 .content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
}

.mobile-home .section4 .content .title,
.mobile-home .section3 .content .title{
    font-size: 22px;
    margin-bottom: 40px;
}

.mobile-home .section4 .content .desc,
.mobile-home .section3 .content .desc{
    font-size: 14px;
    line-height: 24px;
}

.mobile-home .section4 .content .mobile-button2,
.mobile-home .section3 .content .mobile-button2{
    margin-top: 40px;
}

.mobile-home .section4 .h4-slideitem,
.mobile-home .section3 .h3-slideitem{
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.mobile-home .section4 .h4-slideitem > img,
.mobile-home .section3 .h3-slideitem > img{
    height: 100%;
}

/* Section 5 */
.mobile-home.mobile-container .section5{
    display: block;
    padding: 104px 0 0;
}

.mobile-home.mobile-container .section5 .map{
    width: 100%;
    position: relative;
    z-index: 1!important;
    margin-top: 70px;
}

.mobile-home .section5 .content{
    padding: 0 24px;
    width: 100%;
    position: relative;
    z-index: 1;
}

.mobile-home .section5 .content .title,
.mobile-home .section5 .content .group{
    font-size: 13px;
    line-height: 28px;
    margin-bottom: 28px;
}
.mobile-home .section5 .content .lgg{
    margin-top: 40px;
    font-size: 14px;
    line-height: 24px;
    opacity: .7;
}

@keyframes flicker {
    0% { opacity: .2; }
    100% { opacity: 1; }
}